html {
  height: 100%;
}

body {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 20px;
  display: flex;
}

.stopwatch {
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  display: flex;
  position: relative;
}

.stopwatch__fixings-arrows {
  z-index: 2;
  background-color: orange;
  border: 1px solid orange;
  border-radius: 100%;
  width: 7vmin;
  height: 7vmin;
}

.stopwatch__arrow-large {
  z-index: 1;
  transform-origin: bottom;
  background-color: green;
  border: solid green;
  border-radius: 30px;
  width: 1vmin;
  height: 38vmin;
  animation-name: move-large;
  animation-duration: 60s;
  animation-timing-function: linear;
  position: absolute;
  bottom: 40vmin;
}

@keyframes move-large {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.stopwatch__arrow-small {
  transform-origin: bottom;
  background-color: #00f;
  border: solid #00f;
  border-radius: 30px;
  width: 2vmin;
  height: 25vmin;
  animation-name: move-small;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  position: absolute;
  bottom: 40vmin;
}

@keyframes move-small {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(90deg);
  }
}

